  /* Checkout Steps */

.checkout-steps {
  display: flex;
  font-weight: bold;
  justify-content: space-between;
  width: 40rem;
  margin: 1rem auto;
}
.checkout-steps > div {
  border-top: 0.3rem #c0c0c0 solid;
  color: #c0c0c0;
  font-weight: bold;
  flex: 1 1;
  padding-top: 1rem;
}
.checkout-steps > div.active {
  border-top: 0.3rem #f08000 solid;
  color: #f08000;
}

.paymentScreen{
  font-weight: bold;
  padding: 10px;
  padding-top: 20px;
}
.paymentScreen img{
  width: 150px;
  padding-right: 50px;
}